.complete {
    text-decoration: line-through;
    opacity: 0.4;
  }

  .light-color {
    background-color: rgb(101, 109, 74)
  }
  
  .dark-color {
    background-color: rgb(55, 58, 54);
  }
  
  .background-color {
    background-color: rgb(237, 224, 212)
    
  }
  
  .accent-color {
    background-color: rgb(127, 85, 57)
  }
  
  .accent-color-light {
    background-color: rgb(166, 138, 100)
  }

  #todo-button{
    cursor:pointer;
    padding: 3% 6% 3% 4%;
  }
  
   button {
      background-color: rgb(55, 58, 54);
      color: rgb(55, 58, 54);
      cursor: pointer;
      /* color: rgb(237, 224, 212); */
      /* border: rgb(237, 224, 212); */
      
    }

    .dateFont {
      font-size: 50%;
    }

    .note {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      background-color: rgb(101, 109, 74);
      border-radius: 10px;
      padding: 1rem;
      min-height: 170px;
    }
    
    .notes-group {
      display: grid;
      grid-gap: 1rem;
      grid-template-columns: repeat (auto-fit, minmax(245, 1fr));
    }
    
    .note-footer {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .p-2 {
      cursor: pointer;
    }

    .btn {
      color: whitesmoke;
    }

    .calDiv{
      align-content: center;
    }

    .p-4{
      text-align: center;
    }

    .calDiv{
      align-content: center;
    }

    .listItem{
      display: list-item! important;
    }
    .redBorder{
      border-style: inset;
      border-color: rgb(139, 17, 17);
      border-width: thin medium thin;
    }
    .wholeCon{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }

    .todolDiv{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }

    .upbutton{
      color: whitesmoke
    }

   

    .borderNone{
      border: none;
    }
